import React from "react";
import { NavLink } from "react-router-dom";

function Header() {
  return (
    <div className="container-fluid d-flex justify-content-center shadow opacity-75  bg-info position-sticky top-0  ">
      <header className="container">
        <nav className="navbar navbar-dark ">
          <div className="d-flex vw-100 justify-content-between">
            <NavLink
              to="/"
              className="text-light text-decoration-none fw-bold mt-1"
            >
              Employee Management System
            </NavLink>
            <div className="d-flex gap-2 ">
              <NavLink
                to="/add-employees/-1"
                className="text-white text-decoration-none link-info  btn btn-outline-light "
              >
                Add Employee
              </NavLink>
              <NavLink
                to="/findId"
                className="text-white text-decoration-none link-info  btn btn-outline-light  "
              >
                Find
              </NavLink>
              <NavLink
                to="/delete"
                className="text-white text-decoration-none link-info  btn btn-outline-light  "
              >
                Delete
              </NavLink>
            </div>
          </div>
        </nav>
      </header>
    </div>
  );
}

export default Header;
