import "./App.css";
import CreateEmployeeComp from "./components/CreateEmployeeComp";
import DeleteEmployee from "./components/DeleteEmployee";
import Dummy from "./components/Dummy";
// import Dummy from "./components/Dummy";
import EmpList from "./components/EmpList";
import FindByIdComponent from "./components/FindByIdComponent";
import Footer from "./components/Footer";
import Header from "./components/Header";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Router>
        <Header />
        <div>
          <Routes>
            <Route path="/" Component={EmpList}></Route>
            <Route path="/employees" Component={EmpList}></Route>
            <Route path="/findId" Component={FindByIdComponent}></Route>
            <Route path="/delete" Component={DeleteEmployee}></Route>
            <Route
              //step1
              path="/add-employees/:id"
              Component={CreateEmployeeComp}
            ></Route>
          </Routes>
          <Footer />
        </div>
      </Router>

      {/* <Dummy /> */}
    </div>
  );
}

export default App;
