import React, { useEffect, useState } from "react";
import EmployeeService from "../services/EmployeeService";

const DeleteEmployee = () => {
  let [val, setVal] = useState([]);

  let [selected, SetSelected] = useState(0);

  let [type, setType] = useState("id");

  let [condition, setCondition] = useState(false);

  let [result, setResult] = useState("");
  //     {
  //       id: "",
  //       name: "",
  //       email: "",
  //       salary: "",
  //     },
  //   ]);

  //   console.log(result);

  useEffect(() => {
    EmployeeService.getAllIdList()
      .then((result) => {
        console.log(result.data.data);
        setVal(result.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(selected);
    if (!isNaN(selected)) {
      if (selected > 0) {
        console.log("hello");
        EmployeeService.deleteEmployee(selected)
          .then((res) => {
            console.log(res.data.data);
            setResult(res.data.data);
            setCondition(true);
          })
          .catch((error) => {
            setResult(error);
          });
      } else {
        alert("please select the appropriate ID or Name");
      }
    } else {
      EmployeeService.deleteByName(selected)
        .then((res) => {
          console.log(res.data.data);
          setCondition(true);
          setResult(res.data.data);
        })
        .catch((err) => {
          setResult(err);
        });
    }
    console.log(val);
  };

  //to handle the select type of fetch
  let typeHandle = (e) => {
    setCondition(false);
    setType(e.target.value);
  };

  //to handle the fetch for the type
  let dynamicHandler = () => {
    console.log(type);
    if (type === "id") {
      EmployeeService.getAllIdList()
        .then((result) => {
          console.log(result.data.data);
          setVal(result.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      EmployeeService.getAllNameList()
        .then((result) => {
          console.log(result.data.data);
          setVal(result.data.data);
        })
        .catch((err) => console.log(err));
    }
  };

  return (
    <div className="mt-3 container ">
      <div className="container mt-3">
        <form action="" className="mt-3 p-5" onSubmit={handleSubmit}>
          <div className="container rounded shadow border p-4 d-flex flex-column ">
            <span className="fw-bold">Select Employee</span>
            <div className="container">
              <div className="container">
                <label htmlFor="type" className="fw-bold">
                  select type
                </label>
                <select
                  name="type"
                  id="type"
                  className="ms-3 w-25 mt-3 border rounded p-1"
                  value={type}
                  onChange={typeHandle}
                  onMouseLeave={dynamicHandler}
                >
                  <option value="id">ID</option>
                  <option value="name">Name</option>
                </select>
              </div>
              <div className="container">
                <label htmlFor="id" className="fw-bold">
                  select
                </label>
                <select
                  name="id"
                  id="id"
                  className="ms-3 w-25 mt-3 border rounded p-1"
                  value={selected}
                  onChange={(e) => {
                    SetSelected(e.target.value);
                  }}
                >
                  <option defaultValue={"no"}>select {type}</option>
                  {val.map((id) => {
                    return <option key={id}>{id}</option>;
                  })}
                </select>
              </div>
            </div>
            <div>
              <input
                className="mt-3 btn btn-primary"
                type="submit"
                value="Delete"
              />
            </div>
          </div>
        </form>
        {condition ? (
          <div className="container">
            <h4 className="text-center text-success">{result}</h4>
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default DeleteEmployee;
