import axios from "axios";
import React, { Component } from "react";

const GET = "http://localhost:1101/getAll";
const SAVE = "http://localhost:1101/save";
const FetchId = "http://localhost:1101/getEmp?id=";
const UpdateEmployee = "http://localhost:1101/update";
const DeleteEmployee = "http://localhost:1101/delete";
const SearchEmployee = "http://localhost:1101/search?input=";
const GetIdList = "http://localhost:1101/getAllId";
const GetAllNameList = "http://localhost:1101/getAllnames";
const fetchByName = "http://localhost:1101/fetchByName?name=";
const DeleteName = "http://localhost:1101/deleteByName/";

class EmployeeService extends Component {
  getEmployees() {
    return axios.get(GET);
  }
  saveEmployee(employee) {
    return axios.post(SAVE, employee);
  }
  fetchEmployeeId(id) {
    return axios.get(FetchId + id);
  }

  updateEmployee(employee) {
    return axios.put(UpdateEmployee, employee);
  }

  deleteEmployee(id) {
    return axios.delete(DeleteEmployee + "?id=" + id);
  }

  getAllIdList() {
    return axios.get(GetIdList);
  }

  SearchEmployee(name) {
    return axios.get(SearchEmployee + name);
  }

  getAllNameList() {
    return axios.get(GetAllNameList);
  }

  fetchByName(empName) {
    return axios.get(fetchByName + empName);
  }

  deleteByName(empName) {
    return axios.delete(DeleteName + empName);
  }
}
const employeeService = new EmployeeService();
export default employeeService;
