import * as React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { useEffect } from "react";
import EmployeeService from "../services/EmployeeService";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  //   height: 300,
  //   bgcolor: "background.paper",
  borderRadius: "8px",
  boxShadow: 24,
  //   p: 2,
};

export default function ViewEmployeeComp(props) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    fetch();
    setOpen(true);
  };
  const handleClose = () => setOpen(false);

  const [data, setData] = React.useState({});

  const fetch = () => {
    console.log(props.id);
    EmployeeService.fetchEmployeeId(props.id)
      .then((res) => {
        console.log("View employee component fetched data : ", res.data);
        setData(res.data.data);
      })
      .catch((err) => console.log(err));
  };

  return (
    <div>
      <button className="btn btn-success ms-2" onClick={handleOpen}>
        View
      </button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <div className="bg-white shadow p-3 rounded border">
            <div className="card-header d-flex justify-content-center fw-bold ">
              Employee Details
            </div>
            <div className="card-body">
              <div className="row">
                <div className="col">
                  <span className="fw-bold ">Employee ID :</span>
                  <span className="ms-3">{data.id}</span>
                </div>
              </div>

              <div className="row">
                <div className="col">
                  <span className="fw-bold ">Name :</span>
                  <span className="ms-3">{data.name}</span>
                </div>
              </div>

              <div className="row">
                <div className="col">
                  <span className="fw-bold ">e-mail :</span>
                  <span className="ms-3">{data.email}</span>
                </div>
              </div>

              <div className="row">
                <div className="col">
                  <span className="fw-bold ">Salary :</span>
                  <span className="ms-3">{data.salary}</span>
                </div>
              </div>
            </div>
          </div>
        </Box>
      </Modal>
    </div>
  );
}
