import React, { useEffect } from "react";
import EmployeeService from "../services/EmployeeService";
import { useNavigate } from "react-router-dom";
import ViewEmployeeComp from "./ViewEmployeeComp";

const EmpList = (props) => {
  let [employees, setEmployees] = React.useState([]);

  let [search, setSearch] = React.useState("");

  const history = useNavigate();

  useEffect(() => {
    const getAllEmployees = async () => {
      await EmployeeService.getEmployees()
        .then((response) => {
          console.log(response.data.data);
          setEmployees(response.data.data);
        })
        .catch((err) => console.log(err));
    };
    getAllEmployees();
  }, []);

  let addEmployee = () => {
    history("/add-employees/-1");
  };

  //editing the empoyee
  let editEmployee = (id) => {
    console.log(id + "from edit employee");
    history(`/add-employees/${id}`);
  };

  //delete employee
  let deleteEmp = (id) => {
    console.log("hello");
    if (window.confirm("Are you sure to delete this record ?")) {
      EmployeeService.deleteEmployee(id)
        .then((res) => {
          console.log(res);
          alert("Deleted Successfully");
          var newEmps = employees.filter((employee) => employee.id !== id);
          setEmployees(newEmps);
        })
        .catch((error) => alert("Error in deleting" + error));
    }
  };

  //search employee
  let searchData = (e) => {
    e.preventDefault();
    let value = e.target.value;
    // console.log(value);
    setSearch(value);
    // console.log(search + " : search");
    EmployeeService.SearchEmployee(value)
      .then((response) => {
        console.log(response.data.data);
        setEmployees(response.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div>
      <div className="container-fluid mt-3 mb-5">
        <div className="conatiner d-flex flex-column justify-content-center ">
          <h1 className="text-center">Employee List</h1>
          <div className="container">
            <div className="container d-flex justify-content-between   m-0 p-0 mb-2 ">
              <button className="btn btn-primary " onClick={addEmployee}>
                Add Employee
              </button>
              <div className="form-group d-flex  gap-2 align-items-center">
                <label className="fw-medium " htmlFor="name">
                  search
                </label>
                <input
                  className="form-control "
                  type="text"
                  id="name"
                  placeholder="enter employee name"
                  value={search}
                  name="name"
                  onChange={searchData}
                />
              </div>
            </div>
          </div>
          <table className="table table-striped table-bordered ">
            <thead className="border-black border-top">
              <tr>
                <th className="bg-head">Employee ID</th>
                <th className="bg-head">Employee Name</th>
                <th className="bg-head">Employee E-mail</th>
                <th className="bg-head">Employee Salary</th>
                <th className="bg-head">Actions</th>
              </tr>
            </thead>
            <tbody>
              {employees.map((employee) => (
                <tr key={employee.id}>
                  <td>{employee.id}</td>
                  <td>{employee.name} </td>
                  <td>{employee.email}</td>
                  <td>{employee.salary}</td>
                  <td>
                    <div className="container d-flex justify-content-center ">
                      <button
                        className="btn btn-info me-2"
                        onClick={() => {
                          editEmployee(employee.id);
                        }}
                      >
                        Edit
                      </button>
                      <button
                        className="btn btn-danger"
                        onClick={() => deleteEmp(employee.id)}
                      >
                        Delete
                      </button>

                      <ViewEmployeeComp id={employee.id} />
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default EmpList;
