import React, { useEffect, useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import EmployeeService from "../services/EmployeeService";

const FindByIdComponent = () => {
  let [val, setVal] = useState([]);

  let [selected, SetSelected] = useState(0);

  let [type, setType] = useState("id");

  let [condition, setCondition] = useState(false);

  let [employee, setEmployee] = useState([
    {
      id: "",
      name: "",
      email: "",
      salary: "",
    },
  ]);

  console.log(employee);

  useEffect(() => {
    EmployeeService.getAllIdList()
      .then((result) => {
        console.log(result.data.data);
        setVal(result.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(selected);
    if (!isNaN(selected)) {
      if (selected > 0) {
        console.log("hello");
        EmployeeService.fetchEmployeeId(selected).then((res) => {
          console.log(res);
          setEmployee(res.data.data);
          setCondition(true);
        });
      } else {
        alert("please select the appropriate ID or Name");
      }
    } else {
      EmployeeService.fetchByName(selected).then((res) => {
        console.log(res);
        setEmployee(res.data.data);
        setCondition(true);
      });
    }
    console.log(val);
  };

  //to handle the select type of fetch
  let typeHandle = (e) => {
    setCondition(false);
    setType(e.target.value);
  };

  //to handle the fetch for the type
  let dynamicHandler = () => {
    console.log(type);
    if (type === "id") {
      EmployeeService.getAllIdList()
        .then((result) => {
          console.log(result.data.data);
          setVal(result.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      EmployeeService.getAllNameList()
        .then((result) => {
          console.log(result.data.data);
          setVal(result.data.data);
        })
        .catch((err) => console.log(err));
    }
  };

  return (
    <div className="mt-3 container ">
      <div className="container mt-3">
        <form action="" className="mt-3 p-5" onSubmit={handleSubmit}>
          <div className="container rounded shadow border p-4 d-flex flex-column ">
            <span className="fw-bold">Select Employee</span>
            <div className="container">
              <div className="container">
                <label htmlFor="type" className="fw-bold">
                  select type
                </label>
                <select
                  name="type"
                  id="type"
                  className="ms-3 w-25 mt-3 border rounded p-1"
                  value={type}
                  onChange={typeHandle}
                  onMouseLeave={dynamicHandler}
                >
                  <option value="id">ID</option>
                  <option value="name">Name</option>
                </select>
              </div>
              <div className="container">
                <label htmlFor="id" className="fw-bold">
                  select
                </label>
                <select
                  name="id"
                  id="id"
                  className="ms-3 w-25 mt-3 border rounded p-1"
                  value={selected}
                  onChange={(e) => {
                    SetSelected(e.target.value);
                  }}
                >
                  <option defaultValue={"no"}>select {type}</option>
                  {val.map((id) => {
                    return <option key={id}>{id}</option>;
                  })}
                </select>
              </div>
            </div>
            <div>
              <input
                className="mt-3 btn btn-primary"
                type="submit"
                value="Fetch"
              />
            </div>
          </div>
        </form>
        {condition ? (
          <div className="container rounded  border shadow pt-3 pb-3 ">
            <table className="table">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Name</th>
                  <th>E-mai</th>
                  <th>Salary</th>
                </tr>
              </thead>
              <tbody>
                {type === "id" ? (
                  <tr>
                    <td>{employee.id}</td>
                    <td>{employee.name}</td>
                    <td>{employee.email}</td>
                    <td>{employee.salary}</td>
                  </tr>
                ) : (
                  employee.map((emp) => (
                    <tr key={emp.id}>
                      <td>{emp.id}</td>
                      <td>{emp.name}</td>
                      <td>{emp.email}</td>
                      <td>{emp.salary}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default FindByIdComponent;
