import React from "react";
import { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import EmployeeService from "../services/EmployeeService";
import TextField from "@mui/material/TextField";
import {
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
} from "@mui/material";

const CreateEmployeeComp = () => {
  //step2
  let [employee, setEmployee] = useState({
    id: useParams().id,
    name: "",
    email: "",
    salary: "",
  });

  let [error, setError] = useState({
    idError: false,
    nameError: false,
    mailError: false,
    salaryError: false,
  });

  let [title, setTitle] = useState("Add New Employee");
  let [buttonText, setButtonText] = useState("Save");

  //use navigate for navigating between the component
  const history = useNavigate();

  //step3
  useEffect(() => {
    //step 4
    if (employee.id > 0) {
      setTitle("Update Employee");
      setButtonText("Update");
      console.log("hello " + employee.id);
      EmployeeService.fetchEmployeeId(employee.id)
        .then((response) => {
          console.log(response.data.data);
          setEmployee(response.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, []);

  //handling the changes in the form
  const handleInputChange = (e) => {
    e.preventDefault();
    setEmployee({ ...employee, [e.target.name]: e.target.value });
  };

  //Submitting the form
  const submitForm = (e) => {
    e.preventDefault();
    console.log(employee);
    if (!employee.name || !employee.email || !employee.salary) {
      alert("All fields are required!");
    } else {
      //step 5
      if (employee.id > 0) {
        EmployeeService.updateEmployee(employee).then((res) => {
          console.log(res);
          alert(`Employee ${employee.name} is updated!`);
          history("/");
        });
      } else {
        EmployeeService.saveEmployee(employee)
          .then((res) => {
            console.log(res);
            alert(`Employee ${employee.name} is added!`);
            history("/");
          })
          .catch((err) => {
            console.log(err);
          });
      }
    }
  };

  //cancel operation returning to list  of employees
  let canelRequest = () => {
    history("/employees");
  };

  // let getTitle = () => {
  //   if (employee.id > 0) {
  //     setTitle("Update Employee");
  //   } else {
  //     setTitle("Add new employee");
  //   }
  // };

  //form validation
  let handleBlur = (e) => {
    let regx = /^\w+([-]?\w+)*@\w+([-]?\w+)*(\.\w{2,3})+$/;

    if (e.target.name === "email") {
      if (!regx.test(employee.email)) {
        setError({ ...error, mailError: true });
      } else {
        setError({ ...error, mailError: false });
      }
    } else if (e.target.name === "name") {
      if (e.target.name === "" || !isNaN(e.target.value))
        setError({ ...error, nameError: true });
      else setError({ ...error, nameError: false });
    } else if (e.target.name === "salary") {
      // console.log("salary");
      if (employee.salary <= 9999 && employee.salary.length <= 10) {
        setError({ ...error, salaryError: true });
      } else setError({ ...error, salaryError: false });
    }
  };

  return (
    <div className="container mt-5">
      <div className="row d-flex justify-content-center">
        <div className="card col-6 col-md-5 col-sm-10  p-3  shadow ">
          <h3>{title}</h3>
          <div className="card-body ">
            <form
              action=""
              className="d-flex flex-column gap-3"
              onSubmit={submitForm}
            >
              <div className="form-group d-flex  flex-column  gap-2 align-items-start">
                {/* <label htmlFor="name" className="text-start fw-bold mt-3">
                  Name
                </label> */}
                <TextField
                  error={error.nameError === true ? true : false}
                  className="form-control "
                  label="Name"
                  variant="outlined"
                  type="text"
                  id="name"
                  placeholder="your name here"
                  value={employee.name}
                  name="name"
                  onChange={handleInputChange}
                  onBlur={handleBlur}
                  helperText={
                    error.nameError === true ? "enter valid name" : null
                  }
                />

                {/* <input
                  className="form-control "
                  type="text"
                  id="name"
                  placeholder="your name here"
                  value={employee.name}
                  name="name"
                  onChange={handleInputChange}
                  onBlur={handleBlur}
                /> */}
                {/* {error.nameError === true ? (
                  <span className="text-danger">enter valid name</span>
                ) : null} */}
              </div>

              <div className="form-group d-flex flex-column gap-2 align-items-start">
                <TextField
                  error={error.nameError === true ? true : false}
                  className="form-control "
                  label="E-mail"
                  variant="outlined"
                  type="text"
                  id="email"
                  placeholder="your Mail-Id here"
                  value={employee.email}
                  name="email"
                  onChange={handleInputChange}
                  onBlur={handleBlur}
                  helperText={
                    error.mailError === true ? "enter valid mail-id" : null
                  }
                />
                {/* <FormControl>
                  <FormLabel id="demo-radio-buttons-group-label">
                    Gender
                  </FormLabel>
                  <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    defaultValue="female"
                    name="radio-buttons-group"
                  >
                    <FormControlLabel
                      value="female"
                      control={<Radio />}
                      label="Female"
                    />
                    <FormControlLabel
                      value="male"
                      control={<Radio />}
                      label="Male"
                    />
                    <FormControlLabel
                      value="other"
                      control={<Radio />}
                      label="Other"
                    />
                  </RadioGroup>
                </FormControl> */}
              </div>

              <div className="form-group d-flex flex-column gap-2 align-items-start">
                {/* <label htmlFor="salary" className="text-start fw-bold ">
                  Salary
                </label> */}
                <TextField
                  error={error.salaryError === true ? true : false}
                  label="Salary"
                  variant="outlined"
                  className="form-control "
                  type="number"
                  placeholder="200000"
                  id="salary"
                  name="salary"
                  min="0"
                  step=".01"
                  value={employee.salary}
                  onChange={handleInputChange}
                  onBlur={handleBlur}
                  helperText={
                    error.salaryError === true ? "enter valid salary" : null
                  }
                />
                {/* <input
                  className="form-control "
                  type="number"
                  placeholder="200000"
                  id="salary"
                  name="salary"
                  min="0"
                  step=".01"
                  value={employee.salary}
                  onChange={handleInputChange}
                  onBlur={handleBlur}
                /> */}
                {/* {error.salaryError === true ? (
                  <span className="text-danger">enter valid salary</span>
                ) : null} */}
              </div>
              <div className="form-group ms-1 d-flex gap-2">
                <button className="btn btn-success " type="submit">
                  {buttonText}
                </button>
                <button className="btn btn-danger" onClick={canelRequest}>
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreateEmployeeComp;
